/* ------------------------------------ *\
    #BORDER BOX
\* ------------------------------------ */
*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

/* ------------------------------------ *\
    #CUSTOM FONTS
\* ------------------------------------ */
/* ------------------------------------ *\
    #PAGE
\* ------------------------------------ */
html {
  background: #fff;
  color: #000;
  font-family: sans-serif;
  font-size: 14px;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

/* ------------------------------------ *\
    #TYPOGRAPY
\* ------------------------------------ */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 20px;
  font-size: 1.2rem;
  line-height: 1.5; }

p {
  margin: 0 0 20px; }

ul,
ol,
dl {
  margin: 0 0 20px; }

a {
  color: #337ab7;
  text-decoration: none; }
  a:hover, a:active, a:focus {
    text-decoration: underline; }

hr {
  margin: 0 0 20px;
  border: solid #7f7f7f;
  border-width: 0 0 1px; }

/* ------------------------------------ *\
    #IMAGES
\* ------------------------------------ */
img {
  vertical-align: middle;
  margin: 0 0 20px; }

figure {
  margin: 0 0 20px; }

figcaption {
  font-size: 0.85714em;
  font-style: italic; }

/* ------------------------------------ *\
    #FORMS
\* ------------------------------------ */
input,
textarea,
select {
  border: 1px solid #7f7f7f;
  border-radius: 0;
  background: #fff; }



